'use client';

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/shared/ui/Button';
import { LoginForm } from '@/features/auth/ui/LoginForm';
import {
  Card,
  CardContent,
  Typography,
  Box,
  Container,
  Link as MuiLink,
  Chip,
  Avatar,
  Divider,
} from '@mui/material';
import { Grid } from '@mui/material';
import {
  Dashboard as DashboardIcon,
  TrendingUp,
  People,
  Analytics,
  ArrowForward,
} from '@mui/icons-material';
import { APP_CONFIG } from '@/shared/config';
import { LoginCredentials } from '@/features/auth/model/types';
import { User } from '@/entities/user/model/types';

// Mock data for recent dashboards
const mockRecentDashboards = [
  {
    id: '1',
    title: 'Sales Analytics',
    description: 'Track your sales performance and revenue metrics',
    lastUpdated: '2 hours ago',
    type: 'analytics',
    icon: <TrendingUp />,
  },
  {
    id: '2',
    title: 'Team Management',
    description: 'Manage your team members and their performance',
    lastUpdated: '1 day ago',
    type: 'management',
    icon: <People />,
  },
  {
    id: '3',
    title: 'Marketing Overview',
    description: 'Monitor marketing campaigns and conversion rates',
    lastUpdated: '3 days ago',
    type: 'marketing',
    icon: <Analytics />,
  },
];

export const HomePage: React.FC = () => {
  const router = useRouter();
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    // Check authentication status from localStorage
    const token = localStorage.getItem('auth-token');
    if (token) {
      setIsAuthenticated(true);
      setUser({
        id: '1',
        name: 'John Doe',
        email: 'john@example.com',
        role: 'user',
        isActive: true,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
      });
    }
  }, []);

  const handleLogin = async (credentials: LoginCredentials) => {
    setIsLoading(true);
    setError(null);

    try {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1000));

      // Mock successful login
      if (credentials.email && credentials.password) {
        const mockUser: User = {
          id: '1',
          name: 'John Doe',
          email: credentials.email,
          role: 'user',
          isActive: true,
          createdAt: new Date().toISOString(),
          updatedAt: new Date().toISOString(),
        };

        setUser(mockUser);
        setIsAuthenticated(true);
        localStorage.setItem('auth-token', 'dummy-token');
        router.push('/dashboard');
      } else {
        throw new Error('Invalid credentials');
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Login failed');
    } finally {
      setIsLoading(false);
    }
  };

  const handleLogout = () => {
    setIsAuthenticated(false);
    setUser(null);
    localStorage.removeItem('auth-token');
  };

  // Mode 1: Logged in - Show recent dashboards
  if (isAuthenticated) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Box sx={{ mb: 4 }}>
          <Typography variant="h3" component="h1" gutterBottom>
            Welcome back, {user?.name}!
          </Typography>
          <Typography variant="h6" color="text.secondary">
            Here are your recent dashboards
          </Typography>
        </Box>

        <Grid container spacing={3}>
          {mockRecentDashboards.map((dashboard) => (
            <Grid item xs={12} md={4} key={dashboard.id}>
              <Card
                sx={{
                  height: '100%',
                  cursor: 'pointer',
                  transition: 'transform 0.2s, box-shadow 0.2s',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: 4,
                  },
                }}
                onClick={() => router.push(`/dashboard/${dashboard.id}`)}
              >
                <CardContent sx={{ p: 3 }}>
                  <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <Avatar sx={{ bgcolor: 'primary.main', mr: 2 }}>
                      {dashboard.icon}
                    </Avatar>
                    <Box sx={{ flex: 1 }}>
                      <Typography variant="h6" component="h3" gutterBottom>
                        {dashboard.title}
                      </Typography>
                      <Chip
                        label={dashboard.type}
                        size="small"
                        color="primary"
                        variant="outlined"
                      />
                    </Box>
                  </Box>

                  <Typography
                    variant="body2"
                    color="text.secondary"
                    sx={{ mb: 2 }}
                  >
                    {dashboard.description}
                  </Typography>

                  <Box
                    sx={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      alignItems: 'center',
                    }}
                  >
                    <Typography variant="caption" color="text.secondary">
                      Updated {dashboard.lastUpdated}
                    </Typography>
                    <ArrowForward fontSize="small" color="action" />
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>

        <Box sx={{ mt: 4, textAlign: 'center' }}>
          <Button
            variant="outlined"
            size="large"
            onClick={() => router.push('/dashboard')}
            startIcon={<DashboardIcon />}
          >
            View All Dashboards
          </Button>
        </Box>
      </Container>
    );
  }

  // Mode 2: Not logged in - Split screen with title and login form
  return (
    <Box sx={{ minHeight: '100vh', display: 'flex' }}>
      {/* Left side - Application title */}
      <Box
        sx={{
          flex: 1,
          display: { xs: 'none', lg: 'flex' },
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          bgcolor: 'primary.main',
          color: 'primary.contrastText',
          p: 4,
        }}
      >
        <Box sx={{ textAlign: 'center', maxWidth: 600 }}>
          <Typography
            variant="h1"
            component="h1"
            gutterBottom
            sx={{
              fontWeight: 700,
              fontSize: { lg: '4rem', xl: '5rem' },
              lineHeight: 1.2,
            }}
          >
            {APP_CONFIG.name}
          </Typography>

          <Typography
            variant="h4"
            sx={{
              mb: 4,
              opacity: 0.9,
              fontWeight: 300,
            }}
          >
            Your Dashboard, Simplified
          </Typography>

          <Typography
            variant="h6"
            sx={{
              opacity: 0.8,
              fontWeight: 300,
              lineHeight: 1.6,
            }}
          >
            Create beautiful dashboards with ease. Monitor your data, track your
            progress, and make informed decisions with our intuitive dashboard
            platform.
          </Typography>
        </Box>
      </Box>

      {/* Right side - Login form */}
      <Box
        sx={{
          flex: 1,
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          p: 4,
          bgcolor: 'background.default',
        }}
      >
        <Box sx={{ width: '100%', maxWidth: 400 }}>
          {/* Mobile title for small screens */}
          <Box
            sx={{
              display: { xs: 'block', lg: 'none' },
              textAlign: 'center',
              mb: 4,
            }}
          >
            <Typography variant="h3" component="h1" gutterBottom>
              {APP_CONFIG.name}
            </Typography>
            <Typography variant="h6" color="text.secondary">
              Sign in to get started
            </Typography>
          </Box>

          <LoginForm
            onSubmit={handleLogin}
            isLoading={isLoading}
            error={error || undefined}
          />

          <Divider sx={{ my: 3 }}>
            <Typography variant="body2" color="text.secondary">
              New to {APP_CONFIG.name}?
            </Typography>
          </Divider>

          <Box sx={{ textAlign: 'center' }}>
            <Button
              variant="outlined"
              size="large"
              fullWidth
              onClick={() => router.push('/register')}
            >
              Create Account
            </Button>
          </Box>

          <Box sx={{ mt: 4, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              By signing in, you agree to our{' '}
              <MuiLink href="/terms" underline="hover">
                Terms of Service
              </MuiLink>{' '}
              and{' '}
              <MuiLink href="/privacy" underline="hover">
                Privacy Policy
              </MuiLink>
            </Typography>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};
